<template>
  <div class="FirstTrial flex-v">
    <el-breadcrumb separator-class="el-icon-arrow-right"
      class="headertitle">
      <el-breadcrumb-item class="titlecolor">总检台</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="query-form">
      <el-form :inline="true"
        :model="queryObj"
        class="default-form-style"
        size='small'>
        <el-form-item label="登记日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="体检流水号：">
          <el-input v-model="queryObj.sysGuid"
            placeholder="请输入体检流水号"
            style="width: 160px;"
            clearable></el-input>
        </el-form-item>
        <el-form-item label="姓名：">
          <el-input v-model="queryObj.userName"
            placeholder="请输入姓名"
            style="width: 130px;"
            clearable></el-input>
        </el-form-item>
        <el-form-item label="体检状态：">
          <!-- @change='$refs.paginationEvent.initPageList(1)' -->
          <el-select v-model="queryObj.examStatus"
            placeholder="全部"
            @clear='queryObj.examStatus="all"'
            :clearable='queryObj.examStatus!=="all"'>
            <el-option label="全部"
              value="all"></el-option>
            <el-option label="待总检"
              value="waitCheck"></el-option>
            <el-option label="总检成功"
              value="checkSucc"></el-option>
            <el-option label="总检失败"
              value="checkErr"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label='机构名称：'>
          <el-select placeholder='全部'
            v-model='queryObj.createOrganizationId'
            clearable>
            <el-option :value='item.sysGuid'
              v-for='item in orgList'
              :key='item.sysGuid'
              :label='item.oname'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn "
            icon="el-icon-search"
            round
            @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tabledata table-box">
      <el-table stripe
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height='100%'
        v-loading="loading"
        header-cell-class-name='custom-table-cell'>
        <el-table-column width="50"
          label="序号">
          <template v-slot="{$index}">
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label="机构名称"
          prop="createOrganizationName"
          show-overflow-tooltip></el-table-column>
        <el-table-column align="center"
          label="体检流水号"
          show-overflow-tooltip>
          <template v-slot='{row}'>
            <a class="link"
              v-if="row.examStatus == 4 || row.examStatus == 5"
              @click="gotrialpresonprefected(row)">
              {{ row.sysGuid }}
            </a>
            <span v-else>{{ row.sysGuid }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="userName"
          label="姓名"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          label="性别"
          width="120">
          <template v-slot='{row}'>
            {{
              row.sexType == 1 ? '男' :
              row.sexType == 2 ? '女' : '不详'
            }}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="age"
          label="年龄"
          width="80">
        </el-table-column>
        <el-table-column align="center"
          label="总检医生"
          show-overflow-tooltip>
          <template v-slot='{row}'>
            <div>{{row.twoAuthUserName | emptyData}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          label="总检日期"
          width="120">
          <template v-slot='{row}'>
            <div v-if="row.updateDate">{{row.updateDate | formatDate}}</div>
            <div v-else>-</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          label="体检状态"
          width="100">
          <template v-slot='{row}'>
            <label v-if="row.examStatus==3"
              class="daizj">待总检</label>
            <label v-if="row.examStatus==4"
              class="cjwc">总检失败</label>
            <label v-if="row.examStatus==5"
              class="zjloser">总检成功</label>
          </template>
        </el-table-column>
        <el-table-column align="center"
          width="200">
          <template slot="header">
            操作
          </template>
          <template v-slot='{row}'>
            <i class="iconfont bj-await-check"
              @click="audit(row)"
              v-if="row.examStatus==3"></i>
            <i class="iconfont bj-search"
              v-if="row.examStatus==4||row.examStatus==5"
              @click="gotrialpresonprefected(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="queryObj"
        query-url="/user/examUser/allCheckListPaging"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="controlLoading" />
    </div>
  </div>
</template>


<script>
import { formatDate } from '@/lib/utils'
import { mapState } from 'vuex'
export default {
  name: 'TotalCheck',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    let nowDate = formatDate(new Date())
    return {
      loading: false,
      height: '',
      dateRange: [nowDate, `${nowDate} 23:59:59`],
      queryObj: {
        startCreateDate: nowDate,
        endCreateDate: `${nowDate} 23:59:59`,
        sysGuid: '',
        userName: '',
        idNo: '',
        residenceAddress: '',
        examStatus: 'all',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      multipleSelection: [],
      tableData: []
    }
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    controlLoading(loading) {
      this.loading = loading
    },
    onSubmit() {
      this.$refs.paginationEvent.initPageList(1)
    },
    Edit() {},
    audit(row) {
      this.$router.push({
        path: '/conclusionsedit',
        query: {
          examUserId: row.sysGuid
        }
      })
    },
    gotrialpresonprefected(row) {
      this.$router.push({
        path: '/conclusions',
        query: {
          sysGuid: row.sysGuid
        }
      })
    }
  },
  computed: {
    ...mapState(['orgList'])
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startCreateDate = val[0]
        this.queryObj.endCreateDate = val[1] + ' 23:59:59'
      } else {
        this.queryObj.startCreateDate = ''
        this.queryObj.endCreateDate = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.FirstTrial {
  height: 100%;
  .zjloser {
    color: #fd8663;
  }
  .cjwc {
    color: #ffc000;
  }
  .tabledata {
    padding: 0 20px;
    overflow: hidden;
  }
  .daizj {
    color: #2ecc71;
  }
  .tabledata {
    flex: 1;
  }
  .footer {
    display: flex;
    height: 60px;
    background: #fff;
    box-shadow: 0px -2px 3px 0px rgba(0, 67, 151, 0.05);
  }
  .yellowcolor {
    color: #ffb915;
  }
  .slansecolor {
    color: #318bfe;
  }
  .sizecolor {
    color: #318bfe;
  }
}
</style>



